* {
    margin: 0;
    padding: 0;
  }
  *,
  html {
    box-sizing: border-box;
    font-family: Lato, sans-serif;
  }
  html {
    background: #fff;
    background-image: url(https://s3.postimg.org/s1n3ji1ur/paper_fibers_2_X.png);
    font-size: 14px;
    font-weight: 400;
  }
  *,
  :after,
  :before {
    box-sizing: inherit;
  }
  .subtle {
    color: #8f0018;
  }
  .main-card--cointainer {
    grid-gap: 50px;
    grid-template-columns: repeat(3, minmax(300px, 500px));
  }
  .card-container,
  .main-card--cointainer {
    display: grid;
    justify-content: space-around;
  }
  .card-container {
    align-items: flex-start;
    grid-template-columns: 1fr;
    margin: 25px auto 0;
    position: relative;
  }
  .card-container img {
    height: 300px;
    max-width: 100%;
    transition: all 0.5s ease-in-out;
  }
  .card {
    background-color: #fafce3;
    box-shadow: 0 0 5px rgba(75, 75, 75, 0.07);
    padding: 30px;
    position: relative;
    z-index: 1;
  }
  .card-body {
    display: inline-block;
  }
  .card-number {
    margin-top: 15px;
  }
  .card-circle {
    border: 1px solid #cc3f3f;
    border-radius: 50%;
    display: inline-block;
    font-size: 12px;
    height: 25px;
    line-height: 22px;
    text-align: center;
    width: 25px;
  }
  .card-author {
    display: block;
    font-size: 15px;
    letter-spacing: 0.5px;
    margin: 15px 0 0;
    text-transform: uppercase;
  }
  .card-title {
    font-family: Cormorant Garamond, serif;
    font-size: 60px;
    font-weight: 300;
    line-height: 60px;
    margin: 10px 0;
    text-transform: capitalize;
  }
  .card-description {
    font-size: 18px;
    font-weight: 200;
    line-height: 20px;
    margin: 15px 0;
    text-transform: uppercase;
  }
  .card-read {
    cursor: pointer;
    font-size: 14px;
    font-weight: 700;
    letter-spacing: 6px;
    margin: 5px 0 20px;
    position: relative;
    text-align: right;
    text-transform: uppercase;
  }
  .card-read:after {
    background-color: #b8bddd;
    content: "";
    display: block;
    height: 1px;
    position: absolute;
    top: 9px;
    width: 75%;
  }
  .card-tag {
    border: 1px solid #9c1010;
    cursor: pointer;
    float: right;
    margin: 5px 0 0;
    padding: 5px 10px;
    transition: all 0.5s ease-in-out;
  }
  .card-tag:hover {
    background: #f3c8c8;
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
  }
  .card-media:hover {
    -webkit-transform: scale(0.9);
    transform: scale(0.9);
  }
  .card-media {
    height: auto;
    max-width: 100%;
    transition: all 0.5s ease-in-out;
  }
  .card-shadow {
    background-color: #e67575;
    box-shadow: 0 2px 25px 2px #851b1b, 0 2px 50px 2px #921e1e,
      0 0 100px 3px rgba(182, 31, 31, 0.25);
    height: 1px;
    margin: -1px auto 0;
    width: 80%;
    z-index: -1;
  }
  @media (max-width: 998px) {
    .main-card--cointainer {
      grid-gap: 50px;
      display: block;
      grid-template-columns: repeat(1, minmax(200px, 500px));
      justify-content: space-around;
    }
  }
  .Navbar {
    padding-top: 30px;
    text-align: center;
  }
  .Navbar h1:hover {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
  }
  .Navbar h1 {
    padding-bottom: 30px;
    text-transform: capitalize;
    transition: all 0.5s ease-in-out;
  }
  .btn-group__item:hover {
    -webkit-transform: scale(1.15);
    transform: scale(1.15);
  }
  .btn-group {
    border-radius: 1rem;
    text-transform: capitalize;
  }
  .btn-group__item {
    background-color: #f7f6b7;
    border: none;
    box-shadow: inset 0 0 0 -15px #67e049;
    cursor: pointer;
    font-size: 17px;
    margin: 0;
    padding: 1.5rem 3rem;
    text-transform: capitalize;
    transition: all 0.5s ease-in-out;
    transition: all 0.3s ease-out;
  }
  .btn-group__item:last-child {
    border-bottom-right-radius: 1rem;
    border-top-right-radius: 1rem;
  }
  .btn-group__item:first-child {
    border-bottom-left-radius: 1rem;
    border-top-left-radius: 1rem;
  }
  .btn-group__item:focus,
  .btn-group__item:hover {
    box-shadow: inset 0 -20px 0 -15px #d82bad;
    color: #bb0885;
  }
  .btn-group__item:focus {
    outline: none;
  }
  .btn-group__item:after {
    color: #1eca66;
    content: "✔️";
    display: inline-block;
    margin-left: 0.5rem;
    opacity: 0;
    position: absolute;
    -webkit-transform: translatey(10px);
    transform: translatey(10px);
    transition: all 0.2s ease-out;
  }
  .btn-group__item--active:after {
    opacity: 1;
    -webkit-transform: translatey(-2px);
    transform: translatey(-2px);
  }